import fetch from './fetch';
import util from '../utils/utils.js'

let token = '';

function getToken() {
	if (util.getCookie('token')) {
		token = util.getCookie('token');
		// token = JSON.parse(unescape(util.getCookie('token')));
	} else {
		token = '';
	}
}
